import React from 'react'
import styled from 'styled-components';
import Today from './date.js'
import './date.css';
import './App.css';


const Main = styled.div`
font-family: 'Montserrat', sans-serif;
position: relative;
color: red;
`;

const Temp = styled.div`
font-family: 'Montserrat', sans-serif;
color: black;
font-size: 7rem;
padding-left: 31.7rem;
padding-top: 2rem;
`;

const Minmax =  styled.div`
font-family: 'Montserrat', sans-serif;
color: black;
font-size: 1rem;
padding-left: 34.5rem;
`;

const Desp =  styled.div`
font-family: 'Montserrat', sans-serif;
color: black;
font-size: 1rem;
margin-top: 1rem;
position: absolute;
margin-left: 35rem;
`;

const City =  styled.div`
  padding-left: 34.4rem;
  padding-top: 12rem;
  font-size: 1rem;
  color: maroon;
`;







class App extends React.Component{
constructor(props){
  super(props)
  this.state = {
  data : []
  }
}




componentDidMount(){
  const apiKey = '46b32d66c189c6a624b7e6a6f307c7ac'
  fetch(`https://api.openweathermap.org/data/2.5/weather?q=atlanta&appid=${apiKey}&units=metric`)
  .then((resp) => resp.json())
  .then(apiData => {
    this.setState({
      data: {
        temp:apiData.main.temp,
        temp_min:apiData.main.temp_min,
        temp_max:apiData.main.temp_max,
        description: apiData.weather[0].description,
        icon: apiData.weather[0].icon,
        city: apiData.name,
      }
    })
  })
}



render() {

  const icondata = this.state.data.icon
  let iconImg;

  if( icondata === "01n"){
    iconImg = <img src ="http://openweathermap.org/img/wn/01n@2x.png" />
} else 
if(icondata === "01d"){
  iconImg = <img src = "http://openweathermap.org/img/wn/01d@2x.png"/>
} else 
if(icondata === "03n" || icondata === "03d"){
  iconImg = <img src = "http://openweathermap.org/img/wn/03d@2x.png" />
} else 
if(icondata === "02d" ){
 document.getElementById('appicon').src = "http://openweathermap.org/img/wn/02d@2x.png"
} else 
if(icondata === "02n" ){
  iconImg = <img src = "http://openweathermap.org/img/wn/02n@2x.png" />
} else 
if(icondata === "04n" || icondata === "04d"){
  iconImg = <img src = "http://openweathermap.org/img/wn/04d@2x.png" />
} else 
if(icondata === "09n" || icondata === "09d"){
  iconImg = <img src = "http://openweathermap.org/img/wn/09d@2x.png" />
} else 
if(icondata === "13n" || icondata === "13d"){
  iconImg = <img src = "http://openweathermap.org/img/wn/13d@2x.png" />
} else 
if(icondata === "50n" || icondata === "50d"){
  iconImg = <img src = "http://openweathermap.org/img/wn/50d@2x.png" />
} else 
if(icondata === "10d" ){
  iconImg = <img src = "http://openweathermap.org/img/wn/10d@2x.png" />
} else 
if(icondata === "10n" ){
  iconImg = <img src = "http://openweathermap.org/img/wn/10n@2x.png" />
} else 
if(icondata === "11d" ){
  iconImg = <img src = "http://openweathermap.org/img/wn/11d@2x.png" />
} else 
if(icondata === "11n" ){
  iconImg = <img src = "http://openweathermap.org/img/wn/11n@2x.png" />
} else 
iconImg = <img src = "http://openweathermap.org/img/wn/02d@2x.png" />
  
  return(

  <Main>
  
  {iconImg}

    <Temp>
    {Math.round(this.state.data.temp)}&#176;
    </Temp>

    <Minmax>
  {Math.round(this.state.data.temp_min)}&#176;
  <span>/</span>
  {Math.round(this.state.data.temp_max)}&#176;
    </Minmax>


  <Desp>
  {this.state.data.description}
  </Desp>

  <City>
  {this.state.data.city}
  </City>
  
  <Today />

  </Main>



  
  )
}

}



export default App;
